import { get_superiorHospital_info,  get_user_info,} from './api';
import store from '../vuex/store'

function store_notification(we_notification, com_notification){
  let total_notification = false;
  let notification = {};
  if(we_notification===true||com_notification===true){
    total_notification = true;
  }

  notification['we_notification'] = we_notification;
  notification['com_notification'] = com_notification;
  notification['total_notification'] = total_notification;
  store.commit('changeNotification', notification);
}

async function get_message_status() {
  let superior_hospital = localStorage.getItem('superior_hospital');
  let user_id = localStorage.getItem('user_id');
  let para1 = {};
  let para2 = {};
  para1['id'] = superior_hospital;
  para2['id'] = user_id;

  let we_notification = false;
  let com_notification = false;

  await get_superiorHospital_info(para1).then(res=>{
    console.log('res=>',res[0]);
    if(res[0]){
      let message_amount = res[0]['message_amount'];
      if(message_amount > 0 ){
        com_notification = true;
      }
    }else{

    }
  }).catch(error=>{

  });

  await get_user_info(para2).then(res=>{
    console.log('res=>',res[0]);
    if(res[0]){
      let message_amount = res[0]['message_amount'];
      if(message_amount > 0){
        we_notification = true;
      }
    }else{

    }
  }).catch(error=>{

  });

  await store_notification(we_notification, com_notification)

}

export default get_message_status





